/* media queries */

/* 1440px */
@media (max-width: 1440px) {
	html, body {
		font-size: 14px; }

	.container {
		max-width: 1100px; }

	.header {
		&__schedule {
			margin-left: 20%; } }

	.advantages {
		&__sidebar {
			padding: 85px 114px 100px 90px; }

		&__title {
			font-size: 38px; }

		&__subtitle {
			max-width: 80%; } }

	.price {
		&__column {
			max-width: 716px; }

		&__form {
			&-btn {
				padding: 20px 93px; }

			&-text {
				margin-top: 45px; }

			&-desc {
				margin-right: 20px; } } }

	.delivery {
		&__items {
			margin-top: 84px; }

		&__item {
			&-text {
				margin: 40px auto 0 auto; } } }

	.guarantee {
		&__item {
			&-more {
				margin-top: 26px; } } }

	.warehouse {
		&__slide {
			& img {
				width: 92%;
				min-height: 100%; } } }

	.service-maintenance {
		&__column {
			margin: 0px 15px; }

		&__title {
			max-width: 100%; } }

	.free-consultation {
		&__form {
			min-width: 720px; } } }

/* 1366px */
@media (max-width: 1366px) {
	html, body {
		font-size: 13px; }

	.advantages {
		&__sidebar {
			max-width: 550px;
			padding: 76px 50px 82px 84px; }

		&__title {
			font-size: 36px; }

		&__subtitle {
			margin-top: 34px; }

		&__items {
			margin-top: 70px; }

		&__item {
			&:not(:first-child) {
				margin-top: 42px; } } }

	.price {
		&__column {
			max-width: 674px; }

		&__form {
			&-text {
				margin-top: 40px;

				& img {
					max-width: 24px; } }

			&-input {
				&::placeholder {
					font-size: 12px; } }

			&-btn {
				max-width: 320px; } } }

	.delivery {
		&__item {
			&-text {
				margin: 30px auto 0 auto; }

			&-image {
				width: 85px;
				height: 80px; } } }

	.search {
		&__description {
			margin-top: 50px;

			&-text {
				&:not(:first-child) {
					margin: 22px auto 0 auto; } } } }

	.guarantee {
		&__items {
			margin-top: 80px; }

		&__item {
			flex: 0 0 25%;

			&-more {
				font-size: 16px;
				margin-top: 22px; } } }

	.discounts {
		&__items {
			margin-top: 85px; } }

	.questions {
		&__subtitle {
			margin-top: 22px; }

		&__page {
			margin-top: 85px; }

		&__list {
			&-item {
				&:not(:first-child) {
					margin-top: 46px; } } }

		&__more {
			margin: 40px 0 0 20px; } }


	.service-steps {
		&__subtitle {
			margin: 25px auto 0 auto;
			font-size: 18px; }

		&__items {
			margin-top: 85px; }

		&__item {
			&-text {
				margin: 36px auto 0 auto; } } }


	.warehouse {
		&__subtitle {
			margin: 22px auto 0 auto; }

		&__slider {
			margin-top: 85px; } }


	.service-maintenance {
		&__item {
			&-text {
				margin: 32px auto 0 auto; } } }

	.free-consultation {
		&__form {
			min-width: 684px; } }

	.office {
		&__text {
			margin-top: 85px; } } }

/* 1200px */
@media (max-width: $xl) {
	html, body {
		font-size: 10px; }

	.container {
		max-width: 900px; }

	.header {
		&__content {
			padding: 20px; }

		&__schedule {
			margin-left: 10%; }

		&__phone-number {
			& strong {
				font-size: 16px; } }

		&__btn {
			padding: 16px 28px; } }

	.advantages {
		&__sidebar {
			max-width: 524px;
			padding: 60px 75px 65px 71px; }

		&__title {
			font-size: 32px;
			line-height: 48px; }

		&__subtitle {
			max-width: 70%;
			font-size: 22px; }

		&__items {
			margin-top: 55px; } }

	.price {
		&__column {
			max-width: 636px;
			margin-right: 0; }

		&__form {
			&-btn {
				max-width: 300px;
				padding: 20px 80px; } } }

	.delivery {
		&__subtitle {
			margin: 22px auto 0 auto; } }

	.search {
		&__subtitle {
			margin: 18px auto 0 auto; }

		&__form {
			flex-wrap: wrap;
			justify-content: center;

			&-input {
				padding: 5px 20px 2px 20px;

				&:not(:last-child) {
					margin-right: 24px; } }

			&-btn {
				margin-top: 30px; } }

		&__description {
			&-text {
				margin: 10px auto 0 auto; }

			&-phone-number {
				margin-top: 20px; } } }

	.guarantee {
		&__inner {
			max-width: 860px; }

		&__item {
			&-more {
				font-size: 14px; } } }


	.questions {
		&__page {
			&-image {
				& img {
					height: 300px;
					width: 460px; } } }

		&__list {
			&-item {
				max-width: 80%;

				&:not(:first-child) {
					margin-top: 32px; } } }

		&__more {
			margin: 28px 0 0 22px; } }


	.service-steps {
		&__subtitle {
			margin: 14px auto 0 auto;
			font-size: 16px; }

		&__item {
			&-text {
				margin: 28px auto 0 auto; } } }


	.warehouse {
		&__subtitle {
			margin: 14px auto 0 auto; } }

	.free-consultation {
		&__form {
			min-width: 634px; } }

	.office {
		&__text {
			margin-top: 70px; } }

	.footer {
		&__content {
			padding: 30px 0; } } }


@media (max-width: $lg) {
	html, body {
		font-size: 8px; }

	.container {
		max-width: 700px; }

	.logo {
		max-width: 70%; }

	.header {
		&__row {
			justify-content: space-between; }

		&__schedule {
			display: none; }

		&__phone-numbers {
			margin-left: 0; }

		&__phone-number {
			&:not(:first-child) {
				margin-top: 6px; } } }

	.advantages {
		&__sidebar {
			max-width: 460px;
			padding: 40px 110px 45px 60px; }

		&__title {
			line-height: 38px;
			font-size: 30px; }


		&__items {
    		margin-top: 36px; }

		&__subtitle {
			font-size: 20px;
			margin-top: 20px;
			max-width: 80%; }


		&__item {
			&:not(:first-child) {
				margin-top: 34px; } } }

	.price {
		&__column {
			max-width: 550px; }

		&__form {
			&-text {
				margin-top: 28px;
				order: 1;
				margin-right: auto;

				& img {
					display: none; } }

			&-desc {
				margin: 0;
				text-align: center; }

			&-btn {
				margin-left: 25%; } } }


	.delivery {
		&__item {
   		&-image {
   			width: 80px;
   			height: 70px; }

   		&-text {
   			margin: 25px auto 0 auto; } }

		&__subtitle {
			margin: 12px auto 0 auto; } }

	.search {
		&__form {
			&-label {
				span {
					top: 16px;
					right: 45px; } }

			&-input {
				min-height: 50px;
				padding: 0 20px; } } }

	.guarantee {
		&__inner {
			max-width: 680px; }

		&__item {
			&-text {
   			margin: 26px auto 0 auto; }

			&-image {
				height: 250px;
				width: 200px; } } }

	.discounts {
		&__items {
			flex-wrap: wrap;
			margin-top: 30px; }

		&__item {
			flex: 0 0 40%;
			margin-top: 50px;

			&-text {
				margin: 20px auto 0 auto; } } }

	.questions {
		&__page {
			margin-top: 60px;

			&-image {
				display: none; }

			&-description {
				margin-left: 10%; } }

		&__list {
			&-item {
				min-width: 100%;
				padding-left: 14px; } } }

	.service-steps {
		&__column {
			margin: 0; }

		&__flex {
			flex: 0 0 30%; } }

	.service-maintenance {
		&__column {
			margin: 0; }

		&__items {
			margin-top: 84px; }

		&__item {
			flex: 0 0 30%;

			&-image-block {
				height: 160px;
				width: 160px; }


			&-text {
				margin: 24px auto 0 auto; } } }

	.free-consultation {
		&__text {
			margin: 45px auto 0 auto; }

		&__form {
			min-width: 550px;

			&-input {
				padding: 5px 30px 0 31px;

				& + span {
					top: 18px {
   				right: 25px; } } } } }

	.footer {
		&__text {
			&-phone-number {
				margin: 45px auto 0 auto; }

			& a {
				margin-top: 5px;

				& strong {
					font-size: 18px; } }

			&-description {
				font-size: 14px; } } } }

@media (max-width: $md) {
	html, body {
		font-size: 7px; }

	.container {
		max-width: 580px; }

	.header {
		&__phone-numbers {
			display: none; } }

	.price {
   	&__form {
   		margin-top: 50px;

   		&-text {
   			margin-top: 16px; } } }

	.advantages {
		&__sidebar {
			max-width: 400px;
			padding: 34px 62px 36px 60px; }

		&__title {
			font-size: 28px;
			line-height: 32px; }

		&__subtitle {
			min-width: 90%; }

		&__item {
			&:not(:first-child) {
				margin-top: 28px; }

			&-text {
				margin: 0 0 0 20px; } } } }

@media (max-width: $md) {
	.guarantee {
   	&__items {
   		margin-top: 30px;
   		flex-wrap: wrap;
   		justify-content: space-around; }

   	&__item {
   		margin-top: 44px;
   		flex: 0 0 40%;

   		&-more {
   			margin-top: 16px; } } }

	.delivery {
   	&__items {
   		margin-top: 38px;
   		flex-wrap: wrap;
   		justify-content: space-around; }

   	&__item {
   		margin-top: 46px;
   		flex: 0 0 40%;

   		&-text {
   			margin-top: 20px auto 0 auto; } } } }

@media (max-width: $md) {
	.discounts {
   	&__items {
   		justify-content: space-around; } }

	.questions {
   	&__list {
   		&-item {
   			&:not(:first-child) {
   				margin-top: 26px; } } } } }

