.slider {
  margin: 15% auto 0 auto;
  width: 100%;

  &__container {
    max-width: 100%;
    overflow: hidden;
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center; }

  /* slides */
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  /* buttons */
  &__btns {
    width: 95%;
    margin: 0 30px;
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: space-between; }


  &__btn {
    display: inline-block; } }

// swiper slider
.slider__slide img {
  width: 100%;
  height: 100%; }

.swiper-slide img {
  width: 100%;
  min-height: 100%; }

.swiper-button-prev,
.swiper-button-next {
  color: #fff; }


@media (max-width: 1366px) {
  .swiper-slide {
    min-height: 200px; } }

@media (max-width: 992px) {
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 180px; } }

@media (max-width: 660px) {
  .slider {
    max-width: 530px; }

  .swiper-slide {
    max-height: 180px; } }

@media (max-width: 576px) {
  .slider {
    max-width: 420px; } }

@media (max-width: 460px) {
  .slider {
   	max-width: 350px; } }

@media (max-width: 375px) {
  .slider {
   	max-width: 310px; }

  // .swiper-slide img
  //  	width: 92%;

  .slider__buttons {
    display: none; } }

@media (max-width: 320px) {
  .slider {
   	max-width: 300px; } }
