@import '_variables'
@import '_mixins'
@import '_bem'

*, *:before, *:after 
	box-sizing: border-box

html, body 
	font-family: 'ProximaNova'
	font-weight: 400
	background-color: #fff 

.container 
	max-width: 1170px
	margin: 0 auto


/* sections styles */

/* header */
.header
	&__content
		padding: 25px 0 23px 0
		background-color: #191919

	&__row
		display: flex
		align-items: center 

	&__schedule
		margin-left: 352px
		font-size: 12px
		line-height: 24px
		color: #fff
		&-text
			@include ProximaRegular
			span 
				@include ProximaBold

	&__phone-numbers
		margin-left: 155px
		display: flex
		flex-direction: column

	&__phone-number
		text-decoration: none
		color: #fff

		& strong
			@include ProximaBlack
			font-size: 18px
			line-height: 1.2
			color: #fff

		&:not(:first-child)
			margin-top: 14px

	&__btn 
		margin-left: 28px
		background-color: $lightBlue
		padding: 21px 28px

		/* text */
		@include ProximaBold
		font-size: 12px
		line-height: 1.2
		transition: all 0.5s ease-in-out

		&:hover 
			background-color: #312fb9
			border-radius: 5px


/* main */
.main 
	margin: 0

/* advantages */	
.advantages
	background: url('../img/backgrounds/main_bg.png') no-repeat center / cover 

	&__sidebar
		max-width: 570px
		padding: 100px 124px 125px 100px
		background-color: $yellow
		display: flex
		flex-direction: column
		align-items: flex-start

	&__title
		margin: 0 auto
		@include ProximaBlack
		font-size: 40px
		line-height: 55px
		text-transform: uppercase
		color: #000

	&__subtitle
		margin-top: 49px
		margin-bottom: 0
		@include ProximaBold
		font-size: 25px
		line-height: 1.2
		color: #000

	&__items
		margin-top: 100px
		margin-right: 31px

	&__item
		display: flex
		align-items: center
		transform: translateX(100%)
		transition: all 0.8s ease 0s
		opacity: 0

		&:not(:first-child) 
			margin-top: 48px

		&-text
			margin: 0 0 0 25px 
			line-height: 1.2
			color: #000


/* price */
.price
	background: url('../img/backgrounds/price_bg.png') no-repeat center / cover

	&__content
		padding: 6.25rem 0 6.125rem 0

	&__column 
		max-width: 774px
		margin-right: 17px
		margin-left: auto
		display: flex
		flex-direction: column
		align-items: flex-end 

	&__title
		max-width: 520px
		text-align: right 
		transition: all 0.7s ease 0s
		transform: translateY(150%)
		opacity: 0

	&__form
		margin-top: 60px
		display: flex
		justify-content: space-between
		flex-wrap: wrap

		&-text
			max-width: 250px
			margin-top: 55px
			margin-left: auto
			margin-right: 30px
			display: flex
			justify-content: space-between
			align-items: center

		&-desc
			margin-right: 30px
			text-align: right
			color: #000

		&-btn
			margin-top: 30px
			padding: 33px 108px
			background-color: #1866af
			border: none
			cursor: pointer
			transition: all 0.7s linear

			&:hover
				background-color: $blue
				border-radius: 7px

/* delivery */
.delivery 
	&__content
		padding: 6.25rem 0 6.19rem 0
		background-color: $darkBlue

	&__column
		margin: 0 35px
		text-align: center

	&__subtitle
		max-width: 384px
		margin: 30px auto 0 auto

	&__items
		margin-top: 99px
		display: flex
		justify-content: space-between
		align-items: flex-start 

	&__item
		display: flex
		flex-direction: column
		align-items: center
		justify-content: space-between 
		flex: 0 0 20%

		&-image
			width: 95px
			height: 90px

		&-text
			margin: 54px auto 0 auto
			color: #fff

/* search */
.search
	background: url('../img/backgrounds/search_bg.png') no-repeat center / cover 
	&__content
		padding: 6.25rem 0 6.125rem 0

	&__column
		display: flex
		flex-direction: column
		text-align: center

	&__subtitle
		max-width: 618px
		margin: 30px auto 0 auto
		color: #fff

	&__form 
		margin-top: 60px
		display: flex 
		justify-content: space-between
		align-items: center

		&-btn 
			background-color: $yellow

	&__description
		margin-top: 60px

		&-text
			color: #fff

			&:first-child
				margin: 0

			&:not(:first-child)
				margin: 29px auto 0 auto

		& a 
			color: #fff
			text-decoration: none

		&-phone-number
			display: inline-block
			margin-top: 12px

/* guarantee */
.guarantee
	&__content
		padding: 6.25rem 0 6.19rem 0 
		background-color: #fff

	&__inner 
		max-width: 1092px
		margin: 0 auto

	&__column 
		text-align: center

	&__title
		max-width: 70%
		margin: 0 auto

	&__items
		margin-top: 100px
		display: flex
		justify-content: space-between
		align-items: flex-start

	&__item
		display: flex
		flex-direction: column 
		align-items: center
		justify-content: space-between 

		&-image
			width: 230px
			height: 300px

		&-text
			margin: 40px auto 0 auto
			line-height: 1.2

		&-more
			margin-top: 36px
			text-decoration: none
			font-size: 18px


			/* text */
			@include ProximaBold
			color: #000
			text-decoration-color: #000
			position: relative
			text-decoration: underline

			&:after 
				content: ''
				display: inline-block
				background-color: #000
				width: 100% 
				height: 1px
				position: absolute
				bottom: 0
				left: 0

			&:hover
				color: $blue

				&:after
					color: $blue					
				
/* discounts */
.discounts
	&__content
		padding: 6.25rem 0
		background-color: $darkBlue

	&__column 
		margin: 0 17px
		text-align: center

	&__title
		max-width: 72%
		margin: 0 auto

	&__items
		margin-top: 100px
		display: flex
		justify-content: space-between
		align-items: flex-start

	&__item
		display: flex
		flex-direction: column 
		align-items: center
		justify-content: space-between
		flex: 0 0 22%

		&-image-block
			width: 100px
			height: 100px
			border: 2px solid #fff 
			border-radius: 50%
			display: flex
			justify-content: center
			align-items: center
			transition: all 0.5s ease-in-out 

			& span 
				@include ProximaBold
				font-size: 30px
				line-height: 1.2
				color: #fff
				transition: all 0.5s ease-in-out

			&:hover 
				&, & span
					transform: scale(1.05) 

		&-text
			margin: 40px auto 0 auto 
			color: #fff
			line-height: 1.2

/* questions */
.questions
	&__content
		padding: 6.25rem 0 6.31rem
		background-color: $blue

	&__column 
		display: flex
		flex-direction: column
		text-align: center

	&__subtitle
		margin-top: 30px
		color: #fff 

	&__page 
		margin-top: 100px
		display: flex
		justify-content: space-between
		align-items: center

		&-description
			display: flex
			flex-direction: column
			align-items: flex-start
			margin-left: -30px
	
	&__list
		margin: 0

		&-item	
			padding-left: 30px
			position: relative
			
			@include ProximaBold
			font-size: 16px
			line-height: 1.2
			color: #fff	
			text-align: left

			&:not(:first-child)
				margin-top: 55px

			&-link
				color: #fff
				transition: all 0.5s ease-in-out

				&:hover
					color: #579eff

	&__description 
		&-modal
			display: none
			position: absolute
			top: 36px
			right: 0
			bottom: 0
			left: 30px
			z-index: 1
			
	&__more
		margin: 56px 0 0 20px
		display: flex
		justify-content: space-between
		align-items: center

		& span
			margin-left: 19px
			@include ProximaRegular
			font-size: 14px
			text-decoration: underline
			color: #fff

	&__page-image
		position: relative

		& img 
			width: 570px
			height: 390px

		&-shadow
			padding: 12px 29px 11px 19px
			position: absolute
			bottom: 0
			left: 0

		&-desc
			max-width: 182px

			/* text */
			@include ProximaRegular
			font-size: 14px
			color: #fff
			text-align: left


/* service-steps */
.service-steps
	&__content
		padding: 6.25rem 0
		background-color: #fff

	&__column 
		margin: 0 83px
		text-align: center

	&__subtitle
		margin: 30px auto 0 auto 
		@include ProximaRegular
		font-size: 20px
		line-height: 1.2
		color: #000

	&__items
		margin-top: 100px
		display: flex
		justify-content: space-between
		align-items: flex-start 

	&__item
		display: flex 
		flex-direction: column 
		justify-content: space-between
		align-items: center 
		flex: 0 0 25%

		&-image
			width: 88px
			height: 88px

		&-text
			margin: 49px auto 0 auto 
			max-width: 212px
			line-height: 1.2
			color: #000

/* warehouse */
.warehouse
	&__content
		padding: 6.25rem 0 6.19rem 0
		background-color: $blue 

	&__column 
		display: flex
		flex-direction: column 
		text-align: center 

	&__subtitle
		margin: 32px auto 0 auto 
		max-width: 648px
		color: #fff

	// &__slider
	// 	margin-top: 99px
	// 	display: flex
	// 	justify-content: space-between
	// 	align-items: center
	// 	position: relative

	// &__slides
	// 	flex-basis: 100%
	// 	display: flex
	// 	justify-content: space-between
	// 	align-items: center


/* service-maintenance */
.service-maintenance
	&__content
		padding: 6.25rem 0
		background-color: #fff

	&__column 
		margin: 0 40px
		display: flex
		flex-direction: column
		text-align: center

	&__title
		max-width: 90%

	&__items
		margin-top: 100px
		display: flex
		justify-content: space-between
		align-items: flex-start
		
	&__item 
		display: flex
		flex-direction: column 
		justify-content: space-between
		align-items: center 
		flex: 0 0 28%

		&-image-block
			width: 170px
			height: 170px
			border: 5px solid #ffbc3b
			border-radius: 50%
			display: flex
			justify-content: center
			align-items: center 
			transition: all 0.6s ease

			&:hover 
				cursor: pointer
				background-color: #ffbc3b
				transform: translateY(-10%)

		&-image
			max-width: 77px
			min-height: 57px

		&-text
			margin: 40px auto 0 auto 
			line-height: 1.2	
			color: #000	

/* free-consultation */
.free-consultation
	background: url('../img/backgrounds/free_consultation_bg.png') no-repeat center / cover
	
	&__content
		padding: 6.25rem 0 6.19rem 0

	&__inner
		max-width: 100%
		margin: 0 auto

	&__column 
		margin: 0 35px
		display: flex
		flex-direction: column 
		text-align: center 

	&__text
		margin: 60px auto 0 auto
		
		& p
			margin: 0
			color: #fff

	&__phone-number
		display: inline-block
		margin-top: 16px

	&__form 
		min-width: 770px
		margin: 59px auto 0 auto
		display: flex
		flex-wrap: wrap 
		justify-content: space-between
		align-items: center

		&-btn 
			background-color: $yellow

/* office */
.office
	background: url('../img/backgrounds/office_section_bg.png') no-repeat center / cover

	&__content
		padding: 6.25rem 0 6.375rem 0

	&__column 
		display: flex
		flex-direction: column 
		align-items: center
		text-align: center 

	&__text
		margin-top: 101px
		display: flex 
		flex-direction: column 

		&-address
			margin: 0
			line-height: 1.2
			color: #000

		&-phone-number
			margin-top: 34px
			display: inline-block
			@include ProximaBold
			font-size: 24px
			line-height: 1.2
			color: #000

/* footer */
.footer
	&__content 
		padding: 40px 0
		background-color: #191919

	&__row 
		display: flex
		justify-content: space-between
		align-items: center 

	&__text 
		min-width: 60%
		display: flex
		justify-content: space-between
		align-items: center 
		color: #fff

		&-description
			margin: 0
			@include ProximaRegular
			font-size: 16px

			& a 
				@include ProximaBold
				color: #fff 
				text-decoration: none 

		&-column
			display: flex
			flex-direction: column 
			justify-content: space-between
			align-items: center 

		&-desc
			margin: 0
			@include ProximaRegular 
			font-size: 13px
			line-height: 1.2
			color: #fff

		& a 
			margin-top: 13px
			display: inline-block 

		&-phone-number
			@include ProximaBlack
			font-size: 20px

/* animations */
.riding-img 
	animation: riding 0.8s linear 0.2s infinite alternate none

.jumping-img
	animation: moving 0.8s ease-in 0.2s infinite alternate none

.scaled-img
	animation: scaling 1s linear 0.1s infinite alternate none


@keyframes riding 
	0%
		transform: translateX(-5px)
	20%
		transform: translateX(1px)
	40%
		transform: translateX(5px)
	60%
		transform: translateX(9px)
	100%
		transform: translateX(14px)

@keyframes moving
	0%
		transform: translateY(-10%)
	50%
		transform: translateY(15%)
	100%
		transform: translateY(0%)

@keyframes scaling
	0%
		transform: scale(0.8)
	50%
		transform: scale(1.05)
	100%
		transform: scale(1.1)


a strong 
	transition: all 0.4s ease 

	&:hover
		color: $yellow

.visible
	transform: translate(0px, 0px)
	opacity: 1

input 
	@include ProximaRegular
	font-size: 16px
	line-height: 1.2



@import '_media'
@import '_media-mobile'
@import '_slider'
@import '_modal'