/* fonts */
@mixin ProximaRegular {
	font-family: 'Proxima Nova', sans-serif;
	font-weight: normal;
	font-style: normal; }

@mixin ProximaBold {
	font-family: 'Proxima Nova', sans-serif;
	font-weight: 700;
	font-style: normal; }

@mixin ProximaBlack {
	font-family: 'Proxima Nova', sans-serif;
	font-weight: 900;
	font-style: normal;
	/*font-display: swap*/ }
