/* block */
.link-text {
	text-decoration: none;
	color: #fff; }

.shadow {
	background-color: $blue;
	opacity: 0.8; }

/* form */
.form {
	&-label {
		position: relative;
		display: flex;
		align-items: center; }

	&-input-item {
		position: absolute;
		top: 26px;
		right: 35px;
		z-index: 1; }

	&-input {
		border: none;
		background-color: #f2f2f2;
		padding: 26px 30px 26px 31px;

		&:focus {
			outline: none; }

		/* placeholder */
		&::placeholder {
			@include ProximaRegular;
			font-size: 13px;
			line-height: 1.2;
			text-transform: uppercase;
			color: #818181; } }

	&-btn {
		border: none;
		/* text */
		@include ProximaBold;
		font-size: 17px;
		line-height: 1.2;
		text-transform: uppercase;
		color: #fff;
		outline: none; }

	&-desc {
		@include ProximaRegular;
		font-size: 14px;
		line-height: 1.2; }

	&-item {
		min-width: 370px;
		min-height: 70px; } }


/* title text */
.title {
	@include ProximaBlack;
	margin: 0; }

.section-title {
	font-size: 36px; }

.section-mini-title {
	font-size: 30px; }

.section-subtitle {
	@include ProximaRegular;
	font-size: 20px;
	line-height: 1.2;
	margin: 0; }


/* item text */
.item-text {
	@include ProximaRegular;
	font-size: 18px; }

/* text color */
.text-white {
	color: #fff; }

.text-black {
	color: #000; }


/* text */
.mini-desc {
	@include ProximaRegular;
	font-size: 13px;
	line-height: 1.2; }

.text-uppercase {
	text-transform: uppercase; }

.text-underline {
	text-decoration: underline; }

.phone-number {
	@include ProximaBold;
	font-size: 30px;
	line-height: 1.2;
	color: #fff; }


.open-modal {
	animation: slowlyShow 0.5s linear 50ms; }

.animation-item {
	transition: all 0.7s ease 0s;
	opacity: 0; }


.animation-from-top {
	transform: translateY(-120%); }

.animation-from-right {
	transform: translateX(20%); }

.animation-from-bottom {
	transform: translateY(80%); }

.animation-from-left {
	transform: translateX(-140%); }



@keyframes slowlyShow {
	0% {
		opacity: 0.2; }
	25% {
		opacity: 0.3; }
	50% {
		opacity: 0.5; }
	75% {
		opacity: 0.9; }
	100% {
		opacity: 1; } }





/* media queries */

@media (max-width: 1440px) {
	.section {
		&-title {
			font-size: 34px; }

		&-mini-title {
			font-size: 28px; } }

	.form {
		&-item {
			min-width: 340px; } } }



@media (max-width: 1366px) {
	.section {
		&-title {
			font-size: 32px; }

		&-mini-title {
			font-size: 26px; }

		&-subtitle {
			font-size: 18px; } }

	.item {
		&-text {
			font-size: 16px; } }

	.form {
		&-item {
			min-width: 320px;
			max-height: 60px; }

		&-btn {
			font-size: 14px; } }

	.phone-number {
		font-size: 26px; } }


@media (max-width: 1200px) {
	.section {
		&-title {
			font-size: 28px; }

		&-mini-title {
			font-size: 24px; }

		&-subtitle {
			font-size: 16px; } }

	.item {
		&-text {
			font-size: 14px; } }

	.phone-number {
		font-size: 26px; }

	.form {
		&-item {
			min-width: 300px; } } }

@media (max-width: 992px) {
	.section {
		&-title {
			font-size: 26px; }

		&-mini-title {
			font-size: 22px; }

		&-subtitle {
			font-size: 15px; } }

	.form {
		&-item {
			min-width: 260px;
			min-height: 50px; }

		&-input-item {
			top: 22px;
			right: 30px;

			& img {
				width: 80%; } } } }

@media (max-width: $md) {
	.section {
		&-title {
			font-size: 24px; }

		&-mini-title {
			font-size: 20px; }

		&-subtitle {
			font-size: 14px; } } }


@media (max-width: 425px) {
	.section-title {
		line-height: 26px; }

	.form {
		&-desc {
			font-size: 13px; } } }

@media (max-width: 360px) {
	.section {
		&-title {
			font-size: 22px; }

		&-mini-title {
			font-size: 19px; } } }
